.dark-icon{
    position: fixed;
    right: 0;
    top: -10px;
    margin: 2%;
    text-align: center;
}
.dark-icon button{
    background-color: transparent;
    border: none;
    font-size: 30px;
    color: black;
}
.msg1{
    position: absolute;
    color: black;
    margin-top: -5px;
    font-weight: 600;
}
.msg2{
    position: absolute;
    display: none;
    color: white;
    margin-top: -5px;
    font-weight: 600;
}
.msg1{
    display: block;
}
.dark-mode{
    background-image: none;
    background-color: black !important;
    color: white;
}
.dark-icon-white{
    color: white;
}
.msg2-js{
    display: block;
    color: white;
}
.info-name-dark{
    color: #BA68C8;
}
.typing-dark{
    color: #BA68C8;
}
@media only screen and (max-width: 780px){
    .dark-icon{
        position: fixed;
        top: 88%;
        margin: 0;
        color: black;
    }
    .msg1{
        display: none;
    }
}