.skill-card{
    width: max-content;
    padding: 8px 16px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 6px;
    transition: all 0.5s ease-in-out;
}
.skill-icon{
    font-size: 80px;
    /* animation: shakey 1s infinite; */
}
.skill-name{
    font-size: 18px;
    /* animation: zoom 500ms infinite; */
    
}
@keyframes shakey {
    0%{
        transform: scale(1) rotate(0deg);
    }
    25%{
        transform:  scale(1.3) rotate(5deg);
    }
    50%{
        transform: scale(1.3) rotate(-5deg);
    }
    75%{
        transform: scale(1.3) rotate(5deg);
    }
    100%{
        transform: rotate(-5deg);
    }
}

@media only screen and (max-width: 720px) {
    .skill-card{
        width: max-content;
        height: 100px;
    }
    .skill-icon{
        font-size: 60px;
    }
}
.skill-card:hover{
    z-index: 1;
    opacity: 0;
}