.footer{
    padding: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    font-size: 20px;
    background-color: black;
    color: antiquewhite;
    margin-bottom: -35px;
    border-radius: 5px;
    transition: all .3s ease-in-out;
}
#heart{
    font-size: 20px;
    display: none;
}

@media only screen and (max-width: 320px) {
    .footer{
        font-size: 15px;
    }
    #heart{
        font-size: 15px;
    }
}