
.scroll-up {
  position: fixed;
  right: 0;
  bottom: 0px;
  margin: 2%;
}
.scroll-icon{
    height: 40px;
    background-color: white;
    border-radius: 100px;
}
.move{
    -webkit-animation: examp 1s;
    animation: examp 1s;
}
@keyframes examp {
    0%   {bottom:0px;}
  
    100%  {bottom:1000px;}
}

@media only screen and (max-width: 780px) {
    .scroll-up {
        margin: 0;
    }
}
