.header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}
.header-main {
    position: fixed;
    top: 10px;
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 12px;
    width: 80%;
    align-items: center;
    border-radius: 5px;
    background-color: white;
    z-index: 9;
}
.menu{
    display: flex;
    flex: 1;
    justify-content: flex-end;
}
.menu-icon{
    font-size: 24px;
}
.mobile-menu{
    display: none;
}
.web-menu{
    display: flex;
}
.logo{
    display: flex;
    font-size: 36px;
    color: blueviolet;
    font-weight: 800;
}
.side-menu-icon img{
    width: 30px;
}
@media only screen and (max-width: 800px){
    .header{
        margin: auto;
    }
    .mobile{
        left: 50%;
        transform: translate(-50%, 0);
        border-radius: 5px;
    }
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
    .msg1,.msg2{
        display: none;
    }
}