body{
    background-image: linear-gradient(315deg, #e09082 0%, #f8dba4 74%);
}
.home{
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: auto;
}

@media only screen and (max-width: 1024px){
    .home{
        width: 90%;
    }
}@media only screen and (max-width: 512px){
    .home{
        width: 100%;
    }
}