.web{
    display: flex;
    flex: 1;
    align-items: center;
}
.web-option{
    font-size: 20px;
    margin-right: 16px;
    font-weight: 700;
}
.web-option img{
    height: 20px;
}
.web-option a{
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.web-option:last-child{ /*makes the margin-right to 0px for contact option*/
    margin-right: 0px
}