.about{
    margin-top: 50px;
    padding-bottom: 48px;
}
.about-top{
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.about-photo{
    border-top: 1.5px solid black;
    border-radius: 20px;
}
.img{
    height: 400px;
    border-bottom: 2px solid black;
    border-radius: 20px;
    pointer-events: none;
}
.about-info{
    font-size: 24px;
}
.info-name{
    color: blue;
    font-size: 32px;
}
.typing{
    width: 20ch;
    animation: typing 2.5s steps(20), blink .4s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    color: blue;
    font-size: 34px;
}
@keyframes typing {
    from {
      width: 0
    }
}
@keyframes blink {
    50% {
      border-color: transparent
    }
    
}
@media only screen and (max-width: 880px) {
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-photo{
        margin-top: 20px;
}
}
@media only screen and (max-width: 720px) {
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-info{
        font-size: 16px;
    }
    .info-name{
        font-size: 28px;
    }
    .typing{
        width: 250px;
        font-size: 20px;
    }
    .about-photo{
        margin-top: 20px;
    }
    .img{
        height: 250px;
    }
}