.mobile{
    background-color: white;
    height: auto;
    width: 90%;
    position: fixed;
    margin-top: -12px;
    padding: 18px 12px;
    display: grid;
    place-items: center;
    transition: all 0.3s ease-in-out;
}
.close-icon{
    display: flex;
    position: relative;
    font-size: 24px;
}
.close-icon img{
    width: 30px;
}
.mobile-options{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 20px;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.mobile-option{
    font-size: 24px;
    margin: 12px 0px;
    display: flex;
}
.mobile-option img{
    height: 20px;
}
.mobile-option a{
    text-decoration: none;
    color: black;
    display: flex;
}