.contact{
    display: flex;
    flex-direction: column;
    flex: 1;
}
.section-title{
    font-size: 32px;
    font-weight: 700;
}
.contact-container{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
}
.contact-left{
    max-width: 60%;
    font-size: 20px;
    text-align: center;
}
.resume{
    background-color: aquamarine;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 2px solid transparent;
    transition: all 0.1s ease-in-out;
}
.resume a{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    width: 200px;
    text-align: center;
    text-decoration: none;
    color: black;
    
}
.resume:hover{
    background-color: aqua;
    border-radius: 20px;
    border: 2px solid black;
}
@media only screen and (max-width: 720px) {
    .contact-container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .contact-left{
        max-width: 100%;
        font-size: 16px;
        margin-bottom: 12px;
    }
    .resume{
        font-size: 16px;
    }
}