.c-container{
    position: relative;
    margin-bottom: 50px;
}

.c-row{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    text-align: center;
}
.c-column{
    margin: 3%;
    max-width: 720px;
    margin-top: 50px;
}
.c-section-title{
    font-size: 24px;
    font-weight: 600;
}
.c-img{
    width: 350px;
    border: 3px solid white;
    transition: all 0.3s ease-in-out;
}
.c-img:hover{
    border-radius: 20px;
    border: 3px solid black;
}
@media only screen and (max-width: 720px) {
    .c-column{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }
    .c-img{
        width: 250px;
    }
    .c-img:hover{
        transform: none;
    }
}
