.project-card{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}
.project-video{
    width: 40%;
    box-shadow: 2px 2px 20px black;
    transition: all 0.3s ease-in-out;
}
.project-title{
    font-size: 20px;
    font-weight: 600;
}
.project-tags{
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}
.tag{
    padding: 4px 8px;
    background-color: aquamarine;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
    transition: all 0.3s ease-in-out;
    color: black;
}
.tag:hover{
    margin-left: 5px;
    margin-right: 5px;
    transform: scale(1.2);
}
.project-links a{
    text-decoration: none;
    color: black;
}
.project-links{
    margin-top: 6px;
    display: flex;
}
.link-button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    background-color: white;
    border: 2px solid black;
    color: black;
    padding: 10px;
    border-radius: 4px;
    font-size: 16px;
    margin-right: 10px;
    transition: all 0.3s ease-in-out;
}
.link-button2{
    margin-top: 10px;
    margin-bottom: 10px;
    color: black;
    border-radius: 4px;
    font-size: 16px;
    transition: all 0.3s ease-in-out;
}
.link-button:hover, .link-button2:hover{
    border: 2px solid #523BF3;
    background-color: greenyellow;
    border-radius: 60px;
}
.link-button i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
@media only screen and (max-width: 720px) {
    .project-video{
        width: 100%;
        margin-top: 20px;
    }
    .project-card{
        text-align: center;
        flex-direction: column-reverse;
    }
    .project-info{
        margin-left: 0px;
        margin-top: 30px;
    }
    .project-title{
        font-size: 28px;
    }
    .project-tags{
        justify-content: center;
        max-width: 100%;
    }
    .project-links{
        justify-content: center;
    }
    .link-button{
        margin-right: 5px;
    }
}
